.piece {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: 100%;
  cursor: pointer; }
  .piece.black-pawn {
    background-image: url("https://raw.githubusercontent.com/bakuzan/chess-images/master/black_pawn.png"); }
  .piece.black-rook {
    background-image: url("https://raw.githubusercontent.com/bakuzan/chess-images/master/black_rook.png"); }
  .piece.black-knight {
    background-image: url("https://raw.githubusercontent.com/bakuzan/chess-images/master/black_knight.png"); }
  .piece.black-bishop {
    background-image: url("https://raw.githubusercontent.com/bakuzan/chess-images/master/black_bishop.png"); }
  .piece.black-queen {
    background-image: url("https://raw.githubusercontent.com/bakuzan/chess-images/master/black_queen.png"); }
  .piece.black-king {
    background-image: url("https://raw.githubusercontent.com/bakuzan/chess-images/master/black_king.png"); }
  .piece.white-pawn {
    background-image: url("https://raw.githubusercontent.com/bakuzan/chess-images/master/white_pawn.png"); }
  .piece.white-rook {
    background-image: url("https://raw.githubusercontent.com/bakuzan/chess-images/master/white_rook.png"); }
  .piece.white-knight {
    background-image: url("https://raw.githubusercontent.com/bakuzan/chess-images/master/white_knight.png"); }
  .piece.white-bishop {
    background-image: url("https://raw.githubusercontent.com/bakuzan/chess-images/master/white_bishop.png"); }
  .piece.white-queen {
    background-image: url("https://raw.githubusercontent.com/bakuzan/chess-images/master/white_queen.png"); }
  .piece.white-king {
    background-image: url("https://raw.githubusercontent.com/bakuzan/chess-images/master/white_king.png"); }

.read-only .piece {
  cursor: default; }



